<script>
	import Block from "./Block.svelte";
	let timeBlock = [];
	let gMins;
	function decideTime() {
		let date = new Date();
		console.log(date)
		let mins = ( 1440 - (date.getMinutes() + (date.getHours() * 60))) / 15 ;
		gMins = mins;
		
		console.log(mins)
		for ( let i = 96; i > 0; i--){
			timeBlock.push(i)	
		}
	}
	decideTime();
</script>


<style>
	*{
		padding: 0;
		margin: 0;
		background-color: #121921;
		color : whitesmoke;
	}
	main{
		display: inline;
		justify-content: center;
	}
	body{
		background-color: #121921;
		}
	.title{
		font-size: 3.5em;	
		text-align: center;
		padding-top: 1em;
		padding-bottom: 2em;
	}
	.house-div{
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		padding-left: 2em;
		padding-right: 2em;
	}
</style>

<body>
	<main>
		<h1 class="title">A new way to look at time</h1>
		<div class="house-div">
			{#each timeBlock as block,i }
			{#if block > gMins}
			<Block state="{i}" color="black"></Block>
			<!-- {:else if block == gMins}
			<Block state="{i}" color="red"></Block> -->
			{:else}
			<Block state="{i}" color="white"></Block>
			{/if}
			{/each}
		</div>

	</main>
</body>